import { createRouter, createWebHistory } from "vue-router";
import Home from "@/views/Home.vue";
import Login from "@/views/Login.vue";
import Join from "@/views/Join.vue";
import JoinAgree from "@/views/JoinAgree.vue";
import JoinInputs from "@/views/JoinInputs.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
    path: "/login",
    name: "login",
    component: Login
  },
  {
    path: "/join",
    name: "join",
    component: Join,
    children: [
      {
        path: "",
        name: "joinAgree",
        component: JoinAgree
      },
      {
        path: "inputs",
        name: "joinInputs",
        component: JoinInputs
      }
    ]
  }
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
});

export default router;
