<template>
  <main id="login">
    <div class="login-inner">
      <div class="title">
        <Logo />
        <div class="text-wrapper">
          <span>KSR 로그인</span>
          <span>KSR에 오신 것을 환영합니다</span>
        </div>
      </div>
      <form @submit.prevent="fnLogin">
        <div class="join">
          <span>신규 사용자이신가요?&nbsp;</span>
          <RouterLink to="/join">회원가입</RouterLink>
        </div>
        <input type="email" name="userId" v-model="user_id" placeholder="이메일 ID">
        <input type="password" name="userPw" v-model="user_pw" placeholder="비밀번호">
        <button type="submit">로그인</button>
        <div class="option">
          <input id="saveState" type="checkbox" name="saveState">
          <label for="saveState">로그인 상태 유지</label>
          <RouterLink to="/findAccount">아이디/비밀번호 찾기</RouterLink>
        </div>
      </form>
    </div>
  </main>
</template>

<script>
import Logo from "@/components/Logo.vue";

export default {
  components: {Logo},
  data() {
    return {
      user_id: "",
      user_pw: ""
    }
  },
  methods: {
    fnLogin() {
      if (this.user_id === "") {
        alert("ID를 입력하세요.")
        return;
      }

      if (this.user_pw === "") {
        alert("비밀번호를 입력하세요.")
        return;
      }

      alert("로그인되었습니다.")
    }
  }
}
</script>

<style scoped>
@import "@/styles/Login.css";
</style>
