<template>
  <main id="join">
    <div class="join-inner">
      <div class="title">
        <Logo />
        <div class="text-wrapper" v-html="comment" />
      </div>
      <RouterView />
    </div>
  </main>
</template>

<style scoped>
@import "@/styles/Join.css";
</style>
<script setup>
import Logo from "@/components/Logo.vue";

import { computed } from "vue";
import { useRoute } from "vue-router";

const comment = computed(() => {
  if (useRoute().name === "joinAgree") {
    return "<span>회원가입을 위해<br>아래 약관에 동의해주세요</span>";
  } else {
    return "<span>회원가입에 필요한 정보를<br>입력해주세요</span>";
  }
});
</script>
