<template>
  <form id="joinAgree">
    <div class="item">
      <input id="selectAll" type="checkbox" @change="selectAll">
      <label for="selectAll" />
      <div class="text-wrapper">
        <span class="name">모두 동의합니다</span>
        <span class="explain">
          모두 동의는 필수 및 선택 정보에 대한 동의가 포함되어 있으며,<br>
          선택 항목에 동의를 하지 않은 경우에도 서비스를 이용할 수 있습니다
        </span>
      </div>
    </div>
    <div class="item">
      <input id="selectAgreement" type="checkbox" name="agree" @change="offSelectAll">
      <label for="selectAgreement" />
      <div class="text-wrapper">
        <span class="name">
          <mark>(필수)</mark>
          &nbsp;KSR 이용약관 동의
        </span>
      </div>
      <div class="open-detail">
        <a>보기</a>
      </div>
    </div>
    <div class="item">
      <input id="selectPolicy" type="checkbox" name="agree" value="policy" @change="offSelectAll">
      <label for="selectPolicy" />
      <div class="text-wrapper">
        <span class="name">
          <mark>(필수)</mark>
          &nbsp;개인정보 수집 및 이용 동의
        </span>
      </div>
      <div class="open-detail">
        <a>보기</a>
      </div>
    </div>
    <div class="item">
      <input id="selectPromotion" type="checkbox" name="agree" value="policy" @change="offSelectAll">
      <label for="selectPromotion" />
      <div class="text-wrapper">
        <span class="name">
          (선택)&nbsp;프로모션 정보 수집 동의
        </span>
      </div>
      <div class="open-detail">
        <a>보기</a>
      </div>
    </div>
    <RouterLink to="/join/inputs">
      <button type="submit" :disabled="!btnState">다음</button>
    </RouterLink>
  </form>
</template>

<script setup>
import { computed } from "vue";

const selectAll = (e) => {
  const items = document.querySelectorAll("input[name='agree']");

  for (let item of items) {
    if (e.target.checked) {
      item.checked = true;
    } else {
      item.checked = false;
    }
  }
};

const offSelectAll = (e) => {
  const selectAll = document.querySelector("#selectAll");
  const items = document.querySelectorAll("input[name='agree']");
  const checkedItems = document.querySelectorAll("input[name='agree']:checked");

  if (items.length === checkedItems.length) {
    selectAll.checked = true;
  } else {
    selectAll.checked = false;
  }
};
//
// const btnState = computed(() => {
//   const items = document.querySelectorAll("#joinAgree input[name='agree']");
//   const btn = document.querySelector("#joinAgree button[type='submit']");
//
//   return items[0].checked === true && items[1].checked === true
// })
</script>
